import { Helmet } from "react-helmet-async";

import CampaignsContainer from "Containers/Engage/Broadcast";

import CampaignList from "./Broadcast/CampaignList";

const Broadcast = () => {
  return (
    <CampaignsContainer>
      <Helmet>
        <title>Engage Broadcast</title>
      </Helmet>
      <CampaignList />
    </CampaignsContainer>
  );
};

export default Broadcast;
