import {
  Fragment,
  MouseEvent,
  SyntheticEvent,
  useEffect,
  useState,
} from "react";
import { useSearchParams } from "react-router-dom";
import { Box, IconButton, Typography } from "@mui/material";
import { ArrowDropDown } from "@mui/icons-material";

import { useAppState } from "Contexts/App";
import {
  HeaderStartLeadingSx,
  HeaderStartTrailingSx,
} from "Containers/App/LayoutComponents";
import {
  CampaignListContent,
  CampaignListTab,
  CampaignListTabs,
  CampaignListTabsSx,
  CampaignListTabsWrapper,
  CampaignListWrapper,
  CampaignListWrapperSx,
  ListTabButtonSX,
} from "Containers/Engage/Broadcast/LayoutComponents";

import CampaignOptions from "./CampaignOptions";
import { pluralize } from "Utils";

const CampaignList = () => {
  const { setHeaderContent } = useAppState();
  const [searchParams, setSearchParams] = useSearchParams();
  const queryTab = searchParams.get("tab");
  const [currentTab, setCurrentTab] = useState<string>(queryTab ?? "all");
  const [moreOptEl, setMoreOptEl] = useState<HTMLButtonElement | null>(null);
  const openMoreOpt = Boolean(moreOptEl);
  const changeTab = (_event: SyntheticEvent, newValue: string) => {
    setCurrentTab(newValue);
    setSearchParams({ tab: newValue });
  };

  useEffect(() => {
    if (queryTab && queryTab !== currentTab) {
      setCurrentTab(queryTab);
    }
  }, [currentTab, queryTab]);

  useEffect(() => {
    setHeaderContent(
      <Box display="flex" alignItems="center" gap={0.5}>
        <Typography component="span" sx={HeaderStartLeadingSx}>
          {pluralize(0, "Campaign")}
        </Typography>
        <Typography component="span" sx={HeaderStartTrailingSx}>
          {"/"}
        </Typography>
        <Typography component="span" sx={HeaderStartTrailingSx}>
          {currentTab}
        </Typography>
      </Box>
    );

    return () => {
      setHeaderContent(null);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentTab]);

  return (
    <Fragment>
      <CampaignListWrapper sx={CampaignListWrapperSx}>
        <CampaignListTabsWrapper>
          <CampaignListTabs
            value={currentTab}
            onChange={changeTab}
            variant="scrollable"
            orientation="horizontal"
            scrollButtons="auto"
            allowScrollButtonsMobile
            sx={CampaignListTabsSx}
          >
            <CampaignListTab label="All" value="all" />
            <CampaignListTab label="Active" value="active" />
            <CampaignListTab label="Paused" value="paused" />
            <CampaignListTab label="Completed" value="completed" />
          </CampaignListTabs>
          <IconButton
            onClick={(event: MouseEvent<HTMLButtonElement>) =>
              setMoreOptEl(event.currentTarget)
            }
            sx={ListTabButtonSX}
          >
            <ArrowDropDown />
          </IconButton>
        </CampaignListTabsWrapper>
        <CampaignListContent></CampaignListContent>
      </CampaignListWrapper>
      <CampaignOptions
        open={openMoreOpt}
        anchorEl={moreOptEl}
        doClose={() => setMoreOptEl(null)}
      />
    </Fragment>
  );
};

export default CampaignList;
