import { Box, Button, Popper, Typography } from "@mui/material";
import { styled, Theme } from "@mui/system";

export const Wrapper = styled("div")({
  display: "flex",
  height: "100vh",
  justifyContent: "flex-start",
});

export const WrapperSX = {
  flexDirection: { xs: "column-reverse", md: "row" },
};

export const SidebarWrapper = styled("aside")({
  backgroundColor: "#ECF2F5",
  boxShadow: "2px 0px 10px #0000001a",
  display: "flex",
  flexShrink: 1,
  gap: 10,
  position: "relative",
});

export const SidebarWrapperSx = {
  flexDirection: { xs: "row", md: "column" },
  width: { md: 60 },
  zIndex: 1205,
};

export const getSidebarTabsSx = (isMobile: boolean) => ({
  width: { xs: "100%" },

  "& .MuiTabs-flexContainer": {
    gap: 1.25,

    "& .MuiTab-root": {
      backgroundImage: "radial-gradient(transparent, transparent)",
      backgroundPosition: { xs: "center top", md: "center right" },
      backgroundSize: "48px 44px",
      backgroundRepeat: "no-repeat",
      transition: "background-image 1s ease",

      "&.Mui-selected": {
        backgroundImage:
          "radial-gradient(29.66% 29.66% at 50% 50%, #D0F2FF 2.91%, rgba(255, 255, 255, 0) 100%)",
      },

      "&:hover": {
        color: "#12B0EE",
      },
    },
  },

  "&	.MuiTabs-indicator": {
    right: "unset",
    left: 0,
    width: 4,
    display: isMobile ? "none" : "block",
    transition: "top 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
  },
});

export const TabToolTipSX = (theme: Theme) => ({
  '&.MuiTooltip-popper[data-popper-placement*="right"]': {
    "& .MuiTooltip-tooltip": {
      filter: "drop-shadow(4px 2px 8px rgba(0, 0, 0, 0.4))",
      borderRadius: 2,
      fontSize: 16,
      fontWeight: 700,
      lineHeight: "19px",
      ml: 0,
      p: 1.5,
    },
  },
  "& .MuiTooltip-tooltip": {
    bgcolor: theme?.palette.primary.main,

    "& .MuiTooltip-arrow": {
      color: theme?.palette.primary.main,

      "&::before": {
        borderRadius: "1px",
      },
    },
  },
});

