export const BusinessSwitchLabel = ({ label }: { label: string }) => {
  return (
    <Box sx={{ pt: 1.25, px: 2.5 }}>
      <Typography
        component="span"
        sx={{
          fontSize: { xs: 22, md: 14 },
          fontWeight: 700,
          lineHeight: { xs: "26px", md: "16px" },
        }}
      >
        {label}
      </Typography>
    </Box>
  );
};

