import {
  Box,
  BoxProps,
  Button,
  ButtonProps,
  Typography,
  TypographyProps,
} from "@mui/material";
import { styled } from "@mui/system";
import { ArrowRight } from "Assets/Icons";
import { ElementType, Fragment } from "react";

export const FlexBoxFullSpaceBetween = styled(Box)({
  height: "100%",
  display: "flex",
  justifyContent: "space-between",
  width: "100%",
  position: "relative",
});

export const FormTextFieldSX = {
  bgcolor: "#F2F3F7",

  "& .MuiFilledInput-root:hover,& .MuiFilledInput-root.Mui-focused": {
    bgcolor: "#F2F3F7",
  },
  borderRadius: 2,
  height: 52,
};

export const FormTextAreaFieldSX = {
  ...FormTextFieldSX,
  height: "100%",

  "& .MuiFilledInput-root": {
    py: 2.5,
    px: 1.5,
  },
};

export const FlexGridColumnN = ({
  children,
  nosCol,
  ...rest
}: BoxProps & { nosCol: number }) => (
  <Box
    sx={{
      display: "grid",
      gridTemplateColumns: `repeat(${nosCol}, 1fr)`,
      gridRowGap: { xs: 10, xl: 14 },
      gridColumnGap: { xs: 8, xl: 12 },
    }}
    {...rest}
  >
    {children}
  </Box>
);

export const FlexRow = styled("div")({
  display: "flex",
  columnGap: 35,
  rowGap: 30,
});

export const FlexRowSX = {
  flexDirection: { xs: "column-reverse", md: "row" },
};

export const FlexRowGapSX = {
  columnGap: 2.25,
  rowGap: 0,
  flexDirection: { xs: "column-reverse", md: "row" },
};

export const HalfColumn = styled("div")({
  flex: "0 0 auto",
});

export const HalfColumnSX = {
  width: { xs: "100%", md: `calc((100% - 18px)/2)` },
};

export const GrowColumn = styled("div")({
  flexGrow: 1,
});

export const ShrinkColumn = styled("div")({
  flexShrink: 1,
});

const FieldSX = {
  mb: 3.75,
};

export const Field = ({ children, sx }: BoxProps) => {
  return <Box sx={sx ? sx : FieldSX}>{children}</Box>;
};

export const FieldTitle = ({ children, sx, ...rest }: TypographyProps) => (
  <Typography
    sx={{
      color: "#24272CCC",
      fontSize: 14,
      fontWeight: 700,
      lineHeight: "16px",
      mb: 1.75,
      ...(sx ? sx : {}),
    }}
    {...rest}
    children={children}
  />
);

export const ActionArea = ({ children, sx, ...rest }: BoxProps) => (
  <Box
    display="flex"
    justifyContent="flex-end"
    columnGap={1.75}
    alignItems="flex-end"
    sx={sx ? sx : {}}
    {...rest}
  >
    {children}
  </Box>
);

export const ActionButtonSX = {
  bgcolor: "#12B0EE",
  color: "#FFFFFF",
  columnGap: 1.25,
  display: "flex",
  fontSize: 16,
  height: 48,
  mt: 2.5,
  px: 2.5,
  py: 2 - 0.125,

  "&:hover": {
    bgcolor: "#12B0EE",
  },
};

export const ActionButtonColoredSX = {
  color: "#FFFFFF",
  columnGap: 1.25,
  display: "flex",
  fontSize: 16,
  height: 48,
  mt: 2.5,
  px: 2.5,
  py: 2 - 0.125,
};

export const ActionButton = <C extends ElementType>(
  props: ButtonProps<C, { component?: C }> | any
) => {
  const {
    children,
    type = "button",
    color = "inherit",
    variant,
    disableIcon = false,
    disabled,
    icon,
    sx,
    ...rest
  } = props;
  return (
    <Button
      type={type}
      color={color}
      variant={variant}
      disabled={disabled}
      sx={
        variant !== "outlined"
          ? {
              ...(color === "inherit"
                ? { ...ActionButtonSX }
                : { ...ActionButtonColoredSX }),
              ...sx,
            }
          : { ...sx }
      }
      {...rest}
    >
      {children}
      {!disableIcon && (
        <Fragment>
          {icon ? (
            icon
          ) : (
            <ArrowRight color={disabled ? "#00000042" : "#FFFFFFff"} />
          )}
        </Fragment>
      )}
    </Button>
  );
};

export const MediaWrapperSx = {
  width: "100%",
  display: "flex",
  overflow: "hidden",
  position: "relative",
  minHeight: 130,
  alignItems: "center",
  borderTopLeftRadius: 4,
  borderTopRightRadius: 4,
  marginBottom: 3,
  justifyContent: "center",
  backgroundColor: "#C4C4C4",
};
