import { ReactNode } from "react";

import { BroadcastProvider } from "Contexts/Engage";
import { FlexBoxFullSpaceBetween } from "SharedComponents/CommonComponents";

type BroadcastContainerProps = {
  children: ReactNode;
};

const BroadcastContainer = ({ children }: BroadcastContainerProps) => (
  <BroadcastProvider>
    <FlexBoxFullSpaceBetween>{children}</FlexBoxFullSpaceBetween>
  </BroadcastProvider>
);

export default BroadcastContainer;
