import { createContext, FC, useContext, useMemo } from "react";
import { BroadcastApiContextState, BroadcastDataContextState } from "./types";

const BroadcastData = createContext<BroadcastDataContextState | undefined>(
  undefined
);

const BroadcastApi = createContext<BroadcastApiContextState | undefined>(
  undefined
);

const BroadcastProvider: FC<any> = ({ children }) => {
  const data: BroadcastDataContextState = {};
  const api = useMemo<BroadcastApiContextState>(() => ({}), []);

  return (
    <BroadcastData.Provider value={data}>
      <BroadcastApi.Provider value={api}>{children}</BroadcastApi.Provider>
    </BroadcastData.Provider>
  );
};

export const useBroadcastData = () => {
  const context = useContext(BroadcastData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of BroadcastProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useBroadcastApi = () => {
  const context = useContext(BroadcastApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of BroadcastProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default BroadcastProvider;
