import { Menu, MenuItem } from "@mui/material";

import { anchorOrigin, transformOrigin } from "Containers/App/LayoutComponents";
import { CampaignOptionsSX } from "Containers/Engage/Broadcast/LayoutComponents";

type CampaignOptionsProps = {
  open: boolean;
  anchorEl: null | HTMLElement;
  doClose: () => void;
};

const CampaignOptions = ({ open, anchorEl, doClose }: CampaignOptionsProps) => {
  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={doClose}
      onClick={doClose}
      keepMounted={false}
      MenuListProps={{
        "aria-labelledby": "Options",
      }}
      anchorOrigin={anchorOrigin}
      transformOrigin={transformOrigin}
      sx={CampaignOptionsSX}
    >
      <MenuItem>{"New Campaign"}</MenuItem>
      <MenuItem>{"Manage Tabs"}</MenuItem>
    </Menu>
  );
};

export default CampaignOptions;
